@media (min-width: 992px) {
  .fp-container {
    position: relative;
    overflow: hidden;
    height: 100vh;
  }

  .fp-screen,
  .fp-screen-area,
  .fp-scrollbar-track {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  .fp-screen {
    display: none;
    height: 100%;
    transition: transform 1s ease;
  }

  .fp-screen.active {
    display: block;
  }

  .fp-screen.next {
    transform: translate3d(0, 100%, 0);
  }

  .fp-screen.prev {
    transform: translate3d(0, -100%, 0);
  }

  .fp-screen-inner,
  .fp-scrollbar-inner {
    position: relative;
    overflow: hidden;
    height: 100%;
  }

  .fp-scrollbar {
    position: absolute;
    z-index: 9999;
    top: 0;
    right: 0;
    width: 9px;
    height: 100%;
    padding: 3px 2px 3px 0;
  }

  .fp-scrollbar-track {
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
  }
}

